import React, { Component } from 'react';
import "../hub/Hub.css";
import "./Sensor.css";
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';

const baseapi = 'http://' + window.location.hostname + ':1337';
const url = baseapi + "/showsensors/";
const urledit = baseapi + "/editsensor";
const urlcontrol = baseapi + "/hourcontrol/";
let deletesensor = baseapi + "/deletesensor/";

export default class Sensor extends Component {
  _isMounted = false;
  constructor() {
    super();
    this.state = {
      id: '',
      name: '',
      sensor: '',
      measured: '',
      tempis: '',
      gpio: ''
    }
    this.handleNameChange = this.handleNameChange.bind(this);
    this.handleDeleteChange = this.handleDeleteChange.bind(this);
  }

  componentDidMount() {
    this._isMounted = true;
    console.log("I sensor", this.props.location, this.props.location.pathname);
    let pathpart = this.props.location.pathname;
    let id = pathpart.substr(pathpart.lastIndexOf('/') + 1);
    let urlid = url + id;
    deletesensor+= id;

    fetch(urlid)
    .then(response => {
      console.log("response", response);
      return response.json();
    })
    .then(result => {
      if (this._isMounted) {
        console.log(result);
        let tis = result.tempis.toFixed(1);
        let divided = tis.toString();
        divided = divided.replace(".", ",");
        this.setState({name: result.name});
        this.setState({sensor: result.sensor});
        this.setState({id: result.id});
        this.setState({measured: result.measured});
        this.setState({tempis: divided});
        this.setState({gpio: result.gpio});
      }
    })
  }

  componentWillUnmount() {
    this._isMounted = false;
  }

  handleDeleteChange = (eventKey) => {
    console.log('Drop:' + eventKey + ", " + this.state.id);
    fetch(deletesensor)
    .then(res => {
      console.log(res);
      return res.json();
    })
    .then(result => {
        console.log(result);
        this.props.history.push('/sensors');
    })
    .catch(err => {
      console.error(err);
    });
  }

  handleNameChange = (event) => {
    const self = this;

    if (self.state.typingTimeout) {
      clearTimeout(self.state.typingTimeout);
    }

    self.setState({
       name: event.target.value,
       typing: false,
       typingTimeout: setTimeout(function () {
           self.updateSensor("name", self.state.name);
         }, 1500)
    });
    console.log('Name:' + event.target.value);
  }

  updateSensor = (col, val) => {
    console.log(col, val, this.state.id);
    fetch(urledit, {
      method: 'POST',
      body: 'column=' + col + '&value=' + val + '&id=' + this.state.id,
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    })
    .then(res => {
      console.log(res);
      if (res.status === 200) {
        console.log("Uppdaterat", col, val);
        this.setState({
          [col]: val
        });
        console.log(this.state);
      } else {
        const error = new Error(res.error);
        throw error;
      }
      return fetch(urlcontrol + this.state.id)
    })
    .then(response => {
      console.log("response", response);
      return response.json();
    })
    .then(result => {
        console.log(result);
    })
    .catch(err => {
      console.error(err);
    });
  }

  render() {
      let placeholder = this.state.name ? this.state.name : "Lägg till namn";

    return (
      <div className="zones-page">
          <h1>Sensor</h1>
          <p>Senaste mätning:<br />{this.state.measured}</p>
          <Button variant="light" size="lg" block><Form.Control plaintext onKeyUp={this.handleNameChange} type="text" placeholder={placeholder} /></Button>
          <Button variant="light" size="lg" block>Ärvärde: <span className="sensoris">{this.state.tempis}</span> &deg;C</Button>
          <Button variant="light" size="lg" block>Sensorid: <span className="sensoris">{this.state.sensor}</span></Button>
          <Button variant="light" size="lg" block>Gpio: <span className="sensoris">{this.state.gpio}</span></Button>
          <Button variant="light" size="lg" block>Id: <span className="sensoris">{this.state.id}</span></Button>
          <Button variant="light" size="lg" block onClick={this.handleDeleteChange}>Kasta</Button>
          </div>
    );
  }
}
